*{
  box-sizing: border-box;
}
html,
body{
  background: $BgMain;
  ::selection{
    color: white;
    background: #B93A9D;
  }
  scroll-behavior: smooth;
  font-family:$PTSans;
}
.Container{
  max-width: 1366px;
  margin: 0 auto;
}
.Header,.Footer{
  background-color: $BgMain;
  padding: 61px 0 55px;
  .ItemContainer{
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 87px;
    .Nav{
      display: grid;
      grid-row-gap: 39px;
      .Menu{
        display: flex;
        justify-content: center;
       li{
         a{
           text-decoration: none;
           color:$greyLight;
         }
       }
        //topMenu
        &:first-child{
          li{
            margin-right: 40px;
            &:last-child{
              margin-right: 0;
            }
            &:nth-child(3){
              position: relative;
              &:after{
                position: absolute;
                content: '';
                width: 9px;
                height: 100%;
                display: inline-block;
                margin-left: 3px;
                background:  url('../img/header/arrow.svg') center center no-repeat;
              }
            }
            a{
              font-size: 18px;
              line-height: 23px;
            }
          }
        }
        //bottomMenu
        &:last-child{
          li{
            margin-right: 60px;
            &:last-child{
              margin-right: 0;
            }
            a{
              font-size: 24px;
              line-height: 31px;
              white-space: nowrap;
            }
          }
        }
      }
    }
    .Contacts{
      display: flex;
      flex-direction: column;
      .PhoneNumber{
        color:$Purple;
        text-decoration: none;
        font-weight: bold;
        font-size: 18px;
        line-height: 23px;
        white-space: nowrap;
        &:before{
          content: '';
          width: 15px;
          height: 15px;
          display: inline-block;
          background:  url('../img/header/phone.svg') 0 0 no-repeat;
        }
      }
      span{
        margin-top: 14px;
        font-size: 13px;
        line-height: 17px;
        text-align: center;
        color:$Purple;
        white-space: nowrap;
      }
      .socials{
        display: flex;
        margin-top: 14px;
        align-items: center;
        .SocialLink{
          &:last-child{
            margin-right: 0;
          }
          text-decoration: none;
          margin-right: 20px;
        }
      }
    }
  }
}
.Footer{
  background-color: $dark;
}
.arrowLeft,
.arrowRight{
  width: 15px;
  height: 15px;
  border-top: 2px solid $greyLight;
  border-right: 2px solid $greyLight;
  cursor: pointer;
  &:active{
    opacity: 0.3;
  }
}
.arrowLeft{
  transform: rotate(-140deg);
}
.arrowRight{
  transform: rotate(45deg);
}
.controls-item{
  width: 90px;
  height: 42px;
  position: absolute;
  background: rgba(185, 58, 157, 0.7);
  padding: 8px 13px;
  border-radius: 5px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  top: 853px;
  left: 1520px;
  .arrow{
    &:active{
      stroke-opacity:0.3;
    }
  }
}
input{
  margin-top: 8px;
  padding-left: 13px;
  width: 208px;
  height: 32px;
  background: transparent;
  border: 1px solid #ECE8DF;
  box-sizing: border-box;
  border-radius: 5px;
  outline: none;
}
@keyframes fade {
  0%{
    display: flex;
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input[type='number'] {
  -moz-appearance: textfield;
}

textarea:focus, input:focus{
  outline: none;
}