$PTSans:PtSansNarrow;
$BgMain:#38383A;
$greyLight:#ECE8DF;
$Purple:#B93A9D;
$dark:#28282D;
@mixin bigTitle{
  font-family: $PTSans;
  color: $Purple;
  font-weight: bold;
  font-size: 48px;
  line-height: 62px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
}
@mixin mediumTitle{
  font-family: $PTSans;
  font-weight: bold;
  font-size: 20px;
  line-height: 26px;
  text-align: center;
  letter-spacing: 0.02em;
}
@mixin regularText{
  font-family: $PTSans;
  font-weight: normal;
  font-style: normal;
  font-size: 20px;
  line-height: 26px;
}
@mixin smallText{
  font-family: $PTSans;
  font-size: 14px;
  line-height: 18px;
}