}
@media screen and (max-width: 780px){
  .Header{
    padding: 20px 0 35px 0;
    .ItemContainer{
      display: flex;
      align-items: center;
      justify-content: space-between;
      max-width: 728px;
      .logo{
        margin: 25px 0 0 20px;
        position: relative;
        z-index: 6;
        img{
          width: 52px;
          height: 80px;
        }

      }
      .Nav{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: -3000px;
        background-color: $dark;
        height: 100%;
        z-index: 5;
        transition: .3s ease-in-out;
          .Top{
            margin-top: 173px;
          }

      }
      .active{
        top: 0;
        .Menu:last-child{
          flex-direction: column;
          justify-content: center;
          align-items: center;
          li{
            &:first-child{
              margin-top: 0;
            }
            margin-right: 0;
            margin-top: 30px;
            text-align: center;
          }
        }
      }
      .Contacts{
        position: relative;
        z-index: 6;
        .contact-wrapper{
          margin-top: 30px;
          display: flex;
          flex-direction: column;
          .PhoneNumber,span{
            position: relative;
            text-align: left;
            z-index: 6;
          }
          span{
            margin-top: 0;
            display: block;
          }
        }
        .socials{
          display: none;
          a{
            svg{
              width: 25px;
              height: 25px;
            }
          }
        }
        .visible{
          .PhoneNumber,span{
            display: none;
          }
        }
        .unvisible{
          display: block;
        }

      }
      .burger-menu{
        display: block;
        width: 41px;
        position: relative;
        z-index: 6;
        span{
          position: absolute;
          height: 4px;
          width: 100%;
          background: $greyLight;
          display: block;
          border-radius: 5px;
          transition: .3s all;
          &:first-child{
            top: 0;
          }
          &:last-child{
            top: 32px;
          }
          top: 16px;
        }
      }
      .open{
        z-index: 6;
        span{
          &:first-child{
            transform: rotate(45deg);
          }
          &:nth-child(2){
            opacity:0;
          }

          &:last-child{
            transform: rotate(-45deg);
            top: 0;

          }
        }
      }
    }
