@import "~reset-css";
@import "fonts";
@import "vars";
@import "base";
@import "components";

.HomePage {
  &__promo {
    position: relative;
    height: 665px;

    .controls-item {
      position: absolute;
      right: -400px;
      top: 625px;
    }

    .sliderContainer {
      position: relative;
      width: 100%;
      height: 665px;

      .slide-item {
        width: 100%;
        height: 665px;
        display: none;

        &:first-child {
          justify-content: space-between;
          background: $greyLight;

          .sliderInfo {
            display: flex;
            background: $greyLight url("../img/index/index_logo.svg") center center no-repeat;
            flex-basis: 50%;
            flex-direction: column;
            align-items: center;

            .bigTitle {
              margin-top: 97px;
              @include bigTitle;
              font-weight: normal;
            }

            span {
              margin-top: 18px;
              font-family: $PTSans;
              font-style: normal;
              font-weight: bold;
              font-size: 72px;
              line-height: 93px;
              letter-spacing: 0.07em;
              text-transform: uppercase;
              color: $Purple;

            }

            .regularText {
              margin-top: 144px;
              margin-bottom: 30px;
              @include regularText;
            }
          }

          .sliderView {
            position: relative;

            img {
              width: 100%;
              height: 100%;
              object-fit: none;
            }

            &:after {
              content: '';
              display: block;
              position: absolute;
              width: 383px;
              height: 277px;
              background: url('../img/index/slider/tablet.png') 0 0 no-repeat;
              left: -4%;
              top: 63%;
            }
          }
        }

        &:nth-child(2) {
          background: url('../img/index/slider/slider_second_bg.png') center center / cover no-repeat;
          justify-content: center;
          text-align: center;

          .sliderInfo {
            margin-top: 51px;

            .LargeTitle, span {
              font-family: $PTSans;
              font-weight: bold;
              font-size: 72px;
              line-height: 93px;
              letter-spacing: 0.02em;
              text-transform: uppercase;
              color: $greyLight;

              span {
                color: $Purple;
              }
            }

            .bigTitle {
              @include bigTitle;
              color: $greyLight;
            }

            .regularText {
              @include regularText;
              margin-top: 151px;
              color: $greyLight;
            }

            .btn {
              margin-top: 30px;
            }
          }
        }

        &:last-child {
          background: $greyLight url('../img/index/index_logo.svg') 1060px center no-repeat;

          .sliderItemPic {
            &:first-child {
              position: relative;
              margin-left: 230px;
              width: 510px;

              img {
                max-width: 552px;
              }

              &:after {
                content: '';
                position: absolute;
                left: 50%;
                top: 410px;

                display: block;
                width: 75px;
                height: 75px;
                background: url('../img/index/slider/plus.svg') 0 0 no-repeat;
              }
            }

            &:nth-child(2) {
              position: relative;

              &:before {
                content: '';
                display: block;
                position: absolute;
                width: 155px;
                height: 155px;
                top: 423px;
                right: 195px;
                background: url('../img/index/slider/discount.svg') 0 0 no-repeat;

              }

              img {
                height: 100%;
                object-fit: none;
                object-position: 0 229px;

              }
            }
          }

          .sliderInfo {
            margin-top: 117px;
            display: flex;
            flex-basis: 597px;
            flex-direction: column;
            text-align: center;
            align-items: center;

            .bigTitle {
              color: $Purple;
              font-family: $PTSans;
              font-style: normal;
              font-weight: bold;
              font-size: 36px;
              line-height: 47px;
              text-align: center;
              letter-spacing: 0.02em;
              text-transform: uppercase;
            }

            span {
              margin-top: 66px;
              font-family: $PTSans;
              color: $BgMain;
              font-size: 24px;
              line-height: 31px;
              letter-spacing: 0.02em;

              span {
                margin-top: 0;
                font-weight: bold;
                font-size: 96px;
                line-height: 124px;
                letter-spacing: 0.07em;
                text-transform: uppercase;
                color: $Purple;
                display: block;
              }
            }

            .regularText {
              margin-top: 91px;
            }

            .btn {
              background: transparent;
              margin-top: 21px;
              color: $Purple;
              border: 2px solid $Purple;
            }
          }

        }
      }

      .active {
        display: flex;
        animation: fade 3s ease-in-out;
      }
    }
  }

  &__benefits {
    margin-top: 187px;
    background: $dark;
    text-align: center;
    padding-top: 91px;
    padding-bottom: 85px;

    .bigTitle {
      @include bigTitle;
    }

    .benefitsCards {

      display: grid;
      grid-row-gap: 74px;
      justify-content: center;
      flex-wrap: wrap;
      margin: 65px auto;

      .CradsTop, .CardsBottom {
        display: flex;
        justify-content: center;

        .CardItem {
          margin-right: 100px;

          &:last-child {
            margin-right: 0;
          }

          .subtitle {
            font-family: $PTSans;
            font-size: 24px;
            line-height: 31px;
            text-align: center;
            color: $greyLight;
          }
        }
      }

      .CardsBottom {
        .CardItem {
          &:last-child {
            .subtitle {
              width: 184px;
            }
          }

          .subtitle {
            width: 234px;
          }
        }
      }


    }
  }

  &__PopularGoods {
    margin-top: 70px;
    text-align: center;

    .bigTitle {
      @include bigTitle;
    }

    .Goods-container {
      margin: 77px auto;
      max-width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;

      a {
        position: relative;
        transition: 0.2s;
        overflow: hidden;

        &:nth-of-type(1),
        &:nth-of-type(2),
        &:nth-of-type(3) {
          margin-right: 10px;

          .goods-title {
            top: -86px;
            left: 163px;
            transition: 0.5s;
            visibility: hidden;
          }

          &:hover {
            .goods-title {
              top: 148px;
              visibility: visible;
            }
          }
        }

        &:nth-of-type(3) {
          margin-right: 0;
        }

        &:nth-of-type(4),
        &:nth-of-type(5) {
          margin-top: 10px;
          margin-right: 10px;

          .goods-title {
            top: -86px;
            transition: 0.5s;
            visibility: hidden;
          }

          &:hover {
            .goods-title {
              top: 221px;
              visibility: visible;
            }
          }
        }

        &:after {
          content: '';
          z-index: 1;
          left: 0;
          visibility: hidden;
          position: absolute;
          width: 100%;
          height: 0;
          background-color: rgba(185, 58, 157, 0.8);
          transition: 0.5s;
          border-radius: 5px;
        }

        &:hover:after,
        &:active:after {
          visibility: visible;
          height: 99.7%;
        }

        img {
          object-fit: cover;
        }

        .goods-title {
          position: absolute;
          top: 31px;
          left: 280px;
          z-index: 2;
          font-family: $PTSans;
          font-size: 36px;
          line-height: 47px;
          text-align: center;
          text-transform: uppercase;
          color: $greyLight;

          span {
            display: block;
            font-weight: bold;
          }
        }

        .label {
          position: absolute;
          font-family: $PTSans;
          right: -50px;
          bottom: 30px;
          z-index: 2;
          font-weight: bold;
          font-size: 36px;
          line-height: 47px;
          color: rgba(236, 232, 223, 0.4);
          text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
          transform: rotate(90deg);
          visibility: hidden;
          transition: 0.5s;
        }

        &:active,
        &:hover {
          .label {
            right: 30px;
            visibility: visible;
          }
        }

        text-decoration: none;
      }

    }

    .btn {
      margin-top: 50px;
    }
  }

  &__calculate {
    margin-top: 173px;
    padding-bottom: 76px;
    text-align: center;
    background: url('../img/calc/logo_bg.svg') 84% / contain no-repeat;

    .bigTitle {
      @include bigTitle;
    }

    .form {
      display: flex;
      align-items: center;
      margin-top: 37px;
      margin-left: 310px;
      max-width: 927px;
      padding-top: 94px;
      padding-bottom: 118px;
      background: url('../img/calc/form_bg.svg') 0 0 no-repeat;

      .custom-select {
        display: flex;
        flex-direction: column;
        margin-left: 110px;

        .download-image {
          display: flex;
          flex-wrap: wrap;

          .smallText {
            flex-basis: 100%;
            @include smallText();
            text-align: left;
            color: $greyLight;
          }

          #filed {
            display: none;
          }

          .filed-container {
            margin-top: 9px;
            display: flex;
            border: 0.5px solid $greyLight;
            border-radius: 5px;

            span {
              display: inline-block;
              text-align: left;
              width: 372px;
              height: 31px;
              padding: 8px 10px;
              @include smallText();
              color: $greyLight;
            }

            label {
              align-self: flex-end;
              padding: 6px 24px;
              height: 31px;
              background: #B93A9D;
              border-radius: 5px;
              @include smallText();
              color: $greyLight;
            }
          }

        }

        .kitchen-select {
          display: flex;
          max-width: 372px;
          margin-top: 40px;
          padding-bottom: 20px;
          border-bottom: 1px solid $greyLight;
          justify-content: center;

          .kitchen-select-item {
            position: relative;
            width: 102px;

            .smallText {
              @include smallText();
              color: $greyLight;
              margin-bottom: 10px;
            }

            &:last-child {
              margin-right: 0;
            }

            margin-right: 50px;
          }

          .active {
            &:after {
              position: absolute;
              content: '';
              top: 82px;
              display: block;
              width: 102px;
              height: 3px;
              background: $Purple;
              border-radius: 5px;
            }

            .smallText {
              color: $Purple;
            }

            .kitchen-type {
              rect {
                fill: $Purple;
              }
            }
          }
        }

        .kitchen-select-additional {
          display: flex;
          margin-top: 31px;

          .additional-item {
            &:last-child {
              margin-right: 0;
            }

            margin-right: 50px;

            input {
              display: none;
            }

            label {
              margin-right: 37px;

              &:last-child {
                margin-right: 0;
              }

              position: relative;
              @include smallText;
              text-transform: uppercase;
              color: $greyLight;

              &:before {
                content: '';
                display: inline-block;
                width: 9px;
                height: 9px;
                margin-right: 10px;
                border: 1px solid $greyLight;
                border-radius: 5px;
              }

              &:after {
                position: absolute;
                content: '';
                display: inline-block;
                width: 5px;
                height: 5px;
                left: 2.1px;
                top: 5px;
                border: 1px solid $greyLight;
                border-radius: 5px;
                background: $greyLight;
                transition: .2s linear;
                opacity: 0;
              }
            }

            input:checked + label:after {
              opacity: 1;
            }
          }
        }

        .kitchen-options {
          display: flex;
          margin-top: 40px;

          .option-item {
            position: relative;

            &:after {
              content: 'мм';
              top: 32px;
              left: 65px;
              position: absolute;
              @include regularText();
              color: rgba(236, 232, 223, 0.5);
            }

            &:last-child {
              margin-right: 0;
            }

            margin-right: 50px;

            .smallText {
              @include smallText();
              color: $greyLight;
              text-align: left;
              margin-bottom: 6px;
            }

            input {
              width: 90px;
              height: 30px;
              border: 1px solid rgba(236, 232, 223, 0.7);
              background: transparent;
              padding-right: 20px;
              @include regularText();
              color: $greyLight;
              border-radius: 5px;
            }
          }
        }
      }

      .send-mail {
        .mail-item {
          margin-top: 15px;

          &:first-child {
            margin-top: 0;
          }

          .smallText {
            @include smallText();
            color: rgba(236, 232, 223, 0.5);
            text-align: left;
          }
        }

        .btn {
          width: 208px;
          height: 45px;
          margin-top: 67px;
          @include smallText();
        }
      }
    }
  }

  &__about {
