        }

        .regularText {
          margin-top: 14px;
          @include regularText();
          color: $greyLight;
          width: 668px;
          height: 416px;
        }
      }
    }
  }

  &__other {
    margin-top: 140px;
    text-align: center;

    .bigTitle {
      @include bigTitle();
    }

    .OtherMenu {
      margin: 77px auto 0;
      padding-bottom: 15px;
      border-bottom: 1px solid $greyLight;
      max-width: 1080px;
      display: flex;
      justify-content: space-around;

      li {
        position: relative;

        a {
          @include regularText();
          color: $greyLight;
          line-height: 21px;
          text-decoration: none;
          text-transform: uppercase;
        }

        .active {
          color: $Purple;

          &:after {
            content: '';
            display: block;
            top: 37px;
            position: absolute;
            width: 96px;
            height: 3px;
            background: $Purple;
            border-radius: 5px;
          }
        }
      }
