.exampleKitchen {
  &__kitchen-card {
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 1fr 1fr;

    .slide-card {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-column-gap: 20px;

      .slide-show {
        .controls-item {
          display: flex;
          position: relative;
          left: 716px;
          bottom: 29px;
          top: -30px;
          z-index: 1;
        }

        .slide-item {
          .img-item {
            width: 640px;
            height: 463px;

            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
              border-radius: 5px;
            }
          }

          display: none;
        }

        .active {
          display: block;
          animation: fade 3s ease-in-out;
        }
      }

      .dots {
        display: flex;
        position: relative;
        flex-direction: column;
        max-width: 200px;
        @keyframes beforeFade {
          0% {
            height: 0%;
          }
          100% {
            height: 100%;
          }
        }
        span {
          cursor: pointer;
          margin-bottom: 15px;
          position: relative;
          display: block;
          transition: .3s all;

          img {
            width: 200px;
            height: 106px;
            border-radius: 5px;
          }
        }

        .active {
          &:before {
            content: '';
            z-index: 1;
            top: 0;
            left: 0;
            position: absolute;
            width: 100%;
            height: 100%;
            background-color: rgba(185, 58, 157, 0.8);
            transition: 0.5s;
            border-radius: 5px;
            animation: beforeFade .3s;
          }
        }

        .controls-item {
          position: absolute;
          display: none;
          z-index: 2;
          bottom: 0;
          left: 74px;
          right: 0;
          top: 452px;
          width: 52px;
          height: 24px;
          cursor: pointer;

          .arrow {
            width: 8px;
            height: 8px;
          }
        }
      }
    }

    .card-description {
      margin-left: 50px;
      width: 328px;
      align-self: flex-start;
      color: $greyLight;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .card-description-title {
        @include bigTitle();
        color: $greyLight;
        white-space: nowrap;
      }

      .card-description-price {

        margin-top: 44px;
        margin-bottom: 75px;
        font-family: $PTSans;
        font-weight: bold;
        font-size: 52px;
        line-height: 67px;
        color: $Purple;

        span {
          text-align: left;
          display: block;
          font-weight: normal;
        }

        .old-price {
          font-size: 30px;
          line-height: 39px;
          color: $greyLight;

          span {
            display: inline;
            text-decoration: line-through;
          }
        }

        .subtitle {
          font-size: 14px;
          line-height: 18px;
          color: rgba(236, 232, 223, 0.5);
        }
      }

    }
  }

  &__about-kitchen {
    .about-kitchen-card {
      margin: 50px auto;
      max-width: 1080px;
      height: 604px;
      display: flex;
      flex-direction: column;
      background: url("../img/kitchenTemplate/about_logo.png") top no-repeat;

      .selectMenu {
        padding-top: 138px;
        width: 642px;
        display: flex;
        justify-content: space-around;
        padding-bottom: 15px;
        border-bottom: 1px solid #C4C4C4;
        margin-left: 88px;
      }

      p {
        &:first-child {
          width: 410px;
        }

        &:nth-child(3) {
          width: 602px;
        }

        margin-top: 47px;
        width: 640px;
        display: none;
        font-size: 24px;
        line-height: 31px;
        color: $greyLight;
        margin-left: 110px;

        span {
          &:first-child {
            margin-top: 0;
          }

          display: block;
          margin-top: 40px;
        }
      }

      .visible {
        display: block;
        animation: fade 3s ease-in-out;
      }
    }
  }

  &__material-kitchen {
    margin-top: 130px;
    text-align: center;

    .bigTitle {
      @include bigTitle();
    }

    .selectMenu {
      margin: 77px auto;
      width: 860px;
      display: flex;
      justify-content: space-around;
      padding-bottom: 15px;
      border-bottom: 1px solid #C4C4C4;
    }

    .materialMenu {
      margin: 77px auto;
      padding-top: 138px;
      width: 642px;
      display: flex;
      justify-content: space-around;
      padding-bottom: 15px;
      border-bottom: 1px solid #C4C4C4;
    }

    .material-tab {
      display: none;
      align-items: center;

      .mediumTitle {
        flex-basis: 100%;
        margin: 60px 0 30px 0;
        @include mediumTitle();
        color: rgba(236, 232, 223, 0.8);
        font-weight: normal;
      }

      .material-item-row {
        &:first-child {
          margin-top: 0;
        }

        margin-top: 21px;
        display: flex;
        align-items: center;

        .material-tab-item {
          margin-left: 20px;
          overflow: hidden;

          &:first-child {
            margin-left: 0;
          }

          position: relative;
          width: 200px;
          height: 200px;
          transition: .3s all;

          img {
            width: 100%;
            height: 100%;
            object-fit: none;
          }

          .overlay {
            position: relative;
            display: block;
            height: 0;
            opacity: 0;
            transition: .3s ease-in;

            span {
              position: absolute;
              top: -90px;
              left: 0;
              right: 0;
              bottom: 0;
              text-transform: uppercase;
              text-align: center;
              @include mediumTitle();
              color: $greyLight;
              line-height: 21px;
              transition: inherit;
            }

            background: linear-gradient(0deg, rgba(185, 58, 157, 0.75), rgba(185, 58, 157, 0.75)), #51504B;
            border-radius: 5px;
          }

          &:hover {
            .overlay {
              height: 100%;
              opacity: 1;
              cursor: pointer;

              span {
                top: 90px;
              }
            }
          }

          .item-select {
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            z-index: 1;
            cursor: pointer;
            background: linear-gradient(0deg, rgba(185, 58, 157, 0.75), rgba(185, 58, 157, 0.75)), #51504B;
            border-radius: 5px;
            transition: inherit;

            svg {
              content: '';
              position: absolute;
              display: none;
              opacity: 0;
              width: 105px;
              height: 76px;
              top: 52px;
              left: 63px;
              z-index: 2;
              background: url("../img/kitchenTemplate/arrow.svg") center no-repeat;
            }
          }

          .select {
            opacity: 1;

            svg {
              display: block;
              opacity: 1;
            }
          }

        }
      }

      .type-container {
        input {
          display: none;
        }

        label {
          cursor: pointer;
          margin-right: 37px;

          &:last-child {
            margin-right: 0;
          }

          position: relative;
          @include smallText;
          text-transform: uppercase;
          color: $greyLight;

          &:before {
            content: '';
            display: inline-block;
            width: 9px;
            height: 9px;
            margin-right: 10px;
            border: 1px solid $greyLight;
            border-radius: 5px;
          }

          &:after {
            position: absolute;
            content: '';
            display: inline-block;
            width: 4px;
            height: 4px;
            left: 2.3px;
            top: 6px;
            border: 1px solid $greyLight;
            border-radius: 5px;
            background: $greyLight;
            transition: .2s linear;
            opacity: 0;
          }
        }

        input:checked + label:after {
          opacity: 1;
        }

        .type-content {
          margin-top: 38px;
          display: none;
          opacity: 0;
          transition: .3s all;
        }

        #oneLevel:checked ~ .oneLevel,
        #towLevel:checked ~ .towLevel,
        #mezzanine:checked ~ .mezzanine, {
          display: block;
          opacity: 1;
        }
      }
    }

    .visible {
      display: grid;
      animation: fade 3s ease-in-out;
    }

    .btn {
      width: 256px;
      height: 55px;
      margin: 90px auto 105px auto;
      border: 2px solid #B93A9D;
      border-radius: 5px;
      background: none;
      color: $Purple;
    }
  }
}

