.btn{
  display: block;
  width: 208px;
  height: 55px;
  box-sizing: border-box;
  text-decoration: none;
  padding: 15px 43px;
  background-color: $Purple;
  color: $greyLight;
  border-radius: 5px;
  @include mediumTitle();
}
.btn-center{
  margin: 0 auto;
  text-align: center ;
}
.selectMenu,
.materialMenu{
  li{
    a{
      @include regularText;
      line-height: 21px;
      position: relative;
      text-align: center;
      width: 100%;
      text-transform: uppercase;
      color:$greyLight;
      text-decoration: none;
      &:after{
        content: '';
        display: block;
        top: 37px;
        margin: 0 auto;
        position: absolute;
        width: 0;
        height: 3px;
        background: $Purple;
        border-radius: 5px;
        transition: 0.2s ease-in;

      }
    }
  }
  .active{
    &:after{
      width: 100%;
    }
  }
}
.modal {
  background: #28282D;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  z-index: 5;
  display: none;
  overflow: hidden;
  align-items: center;

  &.active {
    display: grid;
  }

  .form-modal {
    &.active {
      display: block;
    }

    width: 545px;
    height: 800px;
    background: #3E393E;
    border-radius: 5px;
    display: none;
    margin: 0 auto;
    text-align: center;
    padding: 60px;
    position: relative;

    .close-area {
      width: 50px;
      height: 50px;
      position: absolute;
      cursor: pointer;
      top: 11px;
      right: 24px;

      .close {
        position: absolute;
        height: 32.25px;
        width: 2px;
        right: 24px;
        background: #28282D;
        top: 11px;
        transform: rotate(45deg);
        cursor: pointer;

        &:before {
          content: '';
          position: absolute;
          height: 32.25px;
          width: 2px;
          left: 0;
          background: #28282D;
          top: 0px;
          transform: rotate(-90deg);
          cursor: pointer;
        }
      }
    }

    form {
      label {
        display: block;
        position: relative;
        margin-top: 40px;

        span {
          font-family: $PTSans;
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 18px;
          color: rgba(236, 232, 223, 0.5);
          position: absolute;
          top: -18px;
          left: 25%;
        }

        input {
          width: 209px;
          padding: 11px 22px;
          height: 45px;
          font-family: $PTSans;
          font-style: normal;
          font-weight: normal;
          font-size: 18px;
          line-height: 23px;
          color: rgba(236, 232, 223, 0.15);
          border: none;
          -moz-border-colors: none;
          border: 1px solid #ECE8DF;
          box-sizing: border-box;
          border-radius: 5px;

          &::placeholder {
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 23px;
            font-family: $PTSans;
            color: rgba(236, 232, 223, 0.15);
          }
        }

        textarea {
          background: transparent;
          border: none;
          width: 209px;
          height: 104px;
          border: 1px solid #ECE8DF;
          box-sizing: border-box;
          border-radius: 5px;
          font-family: $PTSans;
          font-style: normal;
          font-weight: normal;
          font-size: 18px;
          line-height: 23px;
          color: rgba(236, 232, 223, 0.15);
          margin-top: 8px;
          resize: none;
        }

      }

      .send-btn {
        width: 209px;
        height: 54px;
        background: #B93A9D;
        border: none;
        border-radius: 5px;
        outline: none;
        font-family: $PTSans;
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 31px;
        margin-top: 25px;
        cursor: pointer;
        text-align: center;
        color: #ECE8DF;
        transition: background .2s linear;

        &:hover {
          outline: none;
          border: none;
          background: #ba4f9e;
        }

        &:focus, &:active, {
          background: #882b74;
        }
      }

      /* The container */
      .container {
        display: block;
        position: relative;
        width: 210px;
        margin-top: 14px;
        cursor: pointer;
        user-select: none;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        font-family: $PTSans;
        color: rgba(236, 232, 223, 0.6);
        margin-left: auto;
        margin-right: auto;

        .text {
          margin-left: -20px;
          margin-top: 14px;
        }
      }

      /* Hide the browser's default checkbox */
      .container input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
      }

      /* Create a custom checkbox */
      .checkmark {
        position: absolute;
        top: 0;
        left: 0;
        width: 14px;
        height: 14px;
        background: #B93A9D;
        border-radius: 2px;
      }

      /* On mouse-over, add a grey background color */
      .container:hover input ~ .checkmark {
        background-color: #B93A9D;

      }

      /* When the checkbox is checked, add a blue background */
      .container input:checked ~ .checkmark {
        background-color: #B93A9D;
      }

      /* Create the checkmark/indicator (hidden when not checked) */
      .checkmark:after {
        content: "";
        position: absolute;
        display: none;
      }

      /* Show the checkmark when checked */
      .container input:checked ~ .checkmark:after {
        display: block;
      }

      /* Style the checkmark/indicator */
      .container .checkmark:after {
        left: 5px;
        top: 4px;
        width: 2px;
        height: 5px;
        border: solid #ECE8DF .1px;
        border-width: 0 2px 2px 0;
        transform: rotate(45deg);
      }
    }

    h4 {
      font-family: $PTSans;
      font-style: normal;
      font-weight: bold;
      font-size: 36px;
      line-height: 47px;
      text-align: center;
      color: #B93A9D;
    }
  }

  .thx {
    width: 532px;
    height: 354px;
    background: #3E393E;
    margin-left: auto;
    margin-right: auto;
    border-radius: 5px;
    text-align: center;
    padding: 45px;
    display: none;

    &.active {
      display: grid;
    }

    h4 {
      font-style: normal;
      font-weight: bold;
      font-size: 36px;
      line-height: 47px;
      font-family: $PTSans;
      color: #B93A9D;
    }

    span {
      display: block;
      font-style: normal;
      font-weight: normal;
      font-size: 24px;
      line-height: 31px;
      text-align: center;
      font-family: $PTSans;
      color: #ECE8DF;
      margin: 7px auto 0 auto;
      max-width: 253px;
    }

    .btn {
      margin: 33px auto 0 auto;
    }
  }

}
.burger-menu{
  display: none;
}